import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/electric',
    name: 'electric',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/ElectricView.vue')
  },
  {
    path: '/nikon',
    name: 'nikon',
    component: () => import(/* webpackChunkName: "about" */ '../views/NikonView.vue')
  },
  {
    path: '/vips',
    name: 'vips',
    component: () => import(/* webpackChunkName: "about" */ '../views/VipsView.vue')
  },
  {
    path: '/seoul',
    name: 'seoul',
    component: () => import(/* webpackChunkName: "about" */ '../views/SeoulView.vue')
  },
  {
    path: '/pie',
    name: 'pie',
    component: () => import(/* webpackChunkName: "about" */ '../views/ChocoView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
