<template>
    <div @scroll="ballscroll(e)">
        <div class="box-padding-big">
            <div class="container HelloProfile">
                <div class="row">
                    <div class="col-md-12 abiliyTitle">ABILITY</div>
                    <div class="col-sm-3">
                        <img src="../assets/profile/my-Design-1.png" alt="my" class="myIcon">
                    </div>
                    <div class="col-sm-9 iconListBox">
                        <div v-for="(iconList, i) in iconList" :key="iconList" class="iconList" :class="iconClass[i]" @mouseover="profileIcon()">
                            <img :src="require(`../assets/profile/${iconList}.png`)" alt="iconlist" :class="iconImg[i]">
                            {{ icontxt[i] }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="container HelloProfile02">
            <div class="row">
                <div class="col-md-12 doListBox">
                    <div v-for="(doList, i) in doList" :key="doList" class="doList">
                        <img :src="require(`../assets/icon/${doList}.png`)" alt="doList">
                        <div class="abilityBox">
                            <div class="abilityLine" :class="abilityBox[i]"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="row">
                <div class="col-md-12 contactBox">
                    <div class="contactTxt">
                        <h2 class="color01">빠르고 편리하게, 간편하고 쉽게</h2><br>
                        <p class="contactTxt01">사용자 친화적인 웹사이트를 만들고자 노력하고 있습니다.</p>
                    </div>
                    <div class="contact" @click="modal = true">CONTACT ME</div>
                </div>
            </div>
        </div>
        <div class="contactModal" v-if="modal == true" @click="modal = false">
            <div class="contactModalBox">
                <div class="contactModalBox01">
                    <div class="col-sm-2 colLogBox">
                        <div class="logo01">
                            <div class="logoBox00 logoBox05"></div>
                            <div class="logoBox00 logoBox06"></div>
                            <div class="logoBox00 logoBox07"></div>
                            <div class="logoBox00 logoBox08"></div>
                        </div>
                    </div>
                    <div class="col-sm-10 contactModalBox02">
                        <p>NAME : 은승범</p>
                        <p>E-MAIL : hhy3753@naver.com</p>
                        <p>TEL : 010-5187-4525</p>
                    </div>
                </div>
            </div>
        </div>
    </div> <!--temple dive-->
</template>
<script>
import '../css/home.css'
import '../css/common.css'
export default {
    name: 'profileView',
    created () {
        window.addEventListener('scroll', this.handleScroll)
    },
    unmounted () {
        window.removeEventListener('scroll', this.handleScroll)
    },
    data () {
        return {
            iconList: ['roket2', 'puzzle2', 'glue1'],
            iconClass: ['iconList01', 'iconList02', 'iconList03'],
            iconImg: ['iconImg01', 'iconImg02', 'iconImg03'],
            icontxt: ['열정', '계획', '끈기'],
            doList: ['ps', 'ai', 'html', 'css', 'js', 'vue'],
            abilityBox: ['abilityLine01', 'abilityLine02', 'abilityLine03', 'abilityLine04', 'abilityLine05', 'abilityLine06'],
            modal: false
        }
    },
    methods: {
        profileIcon () {
            document.querySelector('.iconList01').addEventListener('mouseover', function () {
                document.querySelector('.iconImg01').classList.add('active')
            })
            document.querySelector('.iconList01').addEventListener('mouseout', function () {
                document.querySelector('.iconImg01').classList.remove('active')
            })
            document.querySelector('.iconList02').addEventListener('mouseover', function () {
                document.querySelector('.iconImg02').classList.add('active')
            })
            document.querySelector('.iconList02').addEventListener('mouseout', function () {
                document.querySelector('.iconImg02').classList.remove('active')
            })
        },
        handleScroll (e) {
        window.addEventListener('scroll', function () {
            const scroll = this.scrollY
            console.log(scroll)
            const iconScroll1 = document.querySelector('.HelloProfile').offsetTop
            const iconScroll2 = scroll + 250
            if (iconScroll2 >= iconScroll1) {
                document.querySelector('.abilityLine01').classList.add('active')
                document.querySelector('.abilityLine02').classList.add('active')
                document.querySelector('.abilityLine03').classList.add('active')
                document.querySelector('.abilityLine04').classList.add('active')
                document.querySelector('.abilityLine05').classList.add('active')
                document.querySelector('.abilityLine06').classList.add('active')
            }
        })
        }
    }
}
</script>
<style scoped>
.HelloProfile {margin-top: 15vw;}
</style>
